
import FlexContainer from 'component/FlexContainer';
import FlexItem from 'component/FlexItem';
import styles from './index.less'

const VerticalDemo = () => {
    return (
        <FlexContainer flex="vertical-full">
            <FlexItem basis={20} className={styles.basis}> 占据20px</FlexItem>
            <FlexItem grow className={styles.grow}>占据剩余的</FlexItem>
        </FlexContainer>
    )
}

export default VerticalDemo;
