import FlexContainer from 'component/FlexContainer';
import FlexItem from 'component/FlexItem';
import styles from './index.less'


const LeftAndRightDemo = () => {
    return (
        <div className={styles.container}>
            <FlexContainer flex="horizontal-full">
                <FlexItem basis={80} className={styles.nav}>侧边栏</FlexItem>
                <FlexItem grow>
                    <FlexContainer flex="vertical-full"> 
                        <FlexItem basis={30} className={styles.text}>头部</FlexItem>
                        <FlexItem grow scroll>
                            <div className={styles.body}>
                                body
                            </div>
                        </FlexItem>
                        <FlexItem basis={30} className={styles.text}>尾部</FlexItem>
                    </FlexContainer>
                </FlexItem>
            </FlexContainer>
        </div>
    )
}


export default LeftAndRightDemo;
