
import FlexContainer from 'component/FlexContainer';
import FlexItem from 'component/FlexItem';
import styles from './index.less'

const HorizontalDemo = () => {
    return (
        <FlexContainer flex="horizontal-full">
            <FlexItem basis={80} className={styles.widthColor}>占据60px</FlexItem>
            <FlexItem grow className={styles.allColor}>
                占用剩余的宽度
            </FlexItem>
            <FlexItem basis={80} className={styles.widthColor}>
                占据80px
            </FlexItem>
        </FlexContainer>
    )
}


export default HorizontalDemo;
