/* eslint-disable import/no-webpack-loader-syntax */
/* eslint-disable @typescript-eslint/no-unused-vars */
import Text from '../../component/Text'
import styles from './index.less'
import Demo from 'component/Demo'

import HorizontalDemo from './HorizontalDemo'
import HorizontalDemoTsx from '!!raw-loader!./HorizontalDemo/index.tsx'
import HorizontalDemoLess from '!!raw-loader!./HorizontalDemo/index.less'

import VerticalDemo from './VerticalDemo'
import VerticalDemoTsx from '!!raw-loader!./VerticalDemo/index.tsx'
import VerticalDemoLess from '!!raw-loader!./VerticalDemo/index.less'

import CenterDemo from './CenterDemo'
import CenterDemoTsx from '!!raw-loader!./CenterDemo/index.tsx';
import CenterDemoLess from '!!raw-loader!./CenterDemo/index.less';

import NavDemo from './NavDemo'
import NavDemoTsx from '!!raw-loader!./NavDemo/index.tsx'
import NavDemoLess from '!!raw-loader!./NavDemo/index.less';

import LeftAndRightDemo from './LeftAndRightDemo'
import LeftAndRightDemoTsx from '!!raw-loader!./LeftAndRightDemo/index.tsx'
import LeftAndRightDemoLess from '!!raw-loader!./LeftAndRightDemo/index.less'
import PropsTable from 'component/PropsTable'


const FlexBoxView = () => {

    return <div>
        {/* FlexBoxView */}
        <section>
            <Text level="h1" className={styles.title}>弹性盒子
            </Text>
            <Text>flex 弹性布局组件，包含弹性容器 FlexContainer 和弹性元素 FlexItem 组合使用。</Text>
        </section>
        <section>
            <Demo
                title="横向排列，纵向居中"
                demo={<HorizontalDemo />}
                codes={[
                    {
                        name: 'index.tsx',
                        type: 'tsx',
                        code: HorizontalDemoTsx
                    },
                    {
                        name: 'index.less',
                        type: 'less',
                        code: HorizontalDemoLess
                    }
                ]} />
        </section>
        <section>
            <Demo

                title="竖向排列，横向占满"
                demo={<VerticalDemo />}
                codes={[
                    {
                        name: 'index.tsx',
                        type: 'tsx',
                        code: VerticalDemoTsx
                    },
                    {
                        name: 'index.less',
                        type: 'less',
                        code: VerticalDemoLess
                    }
                ]}
            />
        </section>

        <section>
            <Demo
                title="全部居中"
                demo={<CenterDemo />}
                codes={
                    [
                        {
                            name: 'index.tsx',
                            type: 'tsx',
                            code: CenterDemoTsx,
                        },
                        {
                            name: 'index.less',
                            type: 'tsx',
                            code: CenterDemoLess
                        }
                    ]
                }
            />
        </section>

        <section>
            <Demo
                title="模拟侧边栏,主界面滚动"
                demo={<NavDemo />}
                codes={
                    [
                        {
                            name: 'index.tsx',
                            type: 'tsx',
                            code: NavDemoTsx,
                        },
                        {
                            name: 'index.less',
                            type: 'less',
                            code: NavDemoLess
                        }
                    ]
                }
            />
        </section>

        <section>
            <Demo
                title="传统左右布局"
                demo={<LeftAndRightDemo />}
                codes={
                    [
                        {
                            name: 'index.tsx',
                            type: 'tsx',
                            code: LeftAndRightDemoTsx

                        },
                        {
                            name: 'index.less',
                            type: 'less',
                            code: LeftAndRightDemoLess
                        }
                    ]
                }
            />
        </section>

        <Text level="h1" className={styles.title}>Props</Text>
        <PropsTable
            title="FlexContainer"
            desc="弹性容器，占据父元素的所有空间，为内部元素提供 Flex 预设布局"
            list={[
                {
                    name: 'flex',
                    desc: '必选，内部元素的预设布局，其他复杂布局请使用flex样式',
                    type: ['vertical-center', 'horizontal-center', 'center', 'vertical-full', 'horizontal-full', 'horizontal-warp'],
                    defValue: '-',
                },
                {
                    name: 'children',
                    desc: '必选，容器内部元素，通常为FlexItem以及其他在flex中可布局的样式元素',
                    type: ['ReactElement', 'string', 'null', 'undefined', '(ReactElement|string|null|undefined)[]'],
                    defValue: '-',
                },
                {
                    name: 'className',
                    desc: '可选，容器的样式，常用于配置非flex布局相关样式',
                    type: 'string',
                    defValue: 'undefined',
                },
                {
                    name: 'onClick',
                    desc: '可选，触发点击事件',
                    type: '(event:ReactMouseEvent)=>void',
                    defValue: 'undefined',
                },
                {
                    name: 'style',
                    desc: '可选，容器的行内样式',
                    type: ' CSSProperties',
                    defValue: '{}',
                },
                {
                    name: 'title',
                    desc: '可选，容器的系统title',
                    type: 'string',
                    defValue: '{}',
                },
            ]}
        />

        <PropsTable
            title="FlexItem"
            desc="弹性元素，一般放置在弹性容器中，指示占用的空间"
            list={[
                {
                    name: 'className',
                    desc: '可选，元素的样式,一般用于配置非flex布局的样式',
                    type: 'string',
                    defValue: 'undefined',
                },
                {
                    name: 'grow',
                    desc: '可选，是否放大',
                    type: 'boolean',
                    defValue: 'false',
                },
                {
                    name: 'basis',
                    desc: '可选，元素占用的空间，使用字符串时为百分比，如：30%',
                    type: ['number', 'string'],
                    defValue: 'undefined',
                },
                {
                    name: 'scroll',
                    desc: '可选，元素是否作为滚动容器',
                    type: 'boolean',
                    defValue: 'false',
                },
                {
                    name: 'children',
                    desc: '可选，内部元素',
                    type: ['ReactElement', 'string', 'null', 'undefined', '(ReactElement|string|null|undefined)[]'],
                    defValue: 'undefined',
                },
                {
                    name: 'onClick',
                    desc: '可选，触发点击事件',
                    type: '(event:ReactMouseEvent)=>void',
                    defValue: 'undefined',
                },
                {
                    name: 'onScroll',
                    desc: '可选，scroll为true时使用，scroll滚动事件触发',
                    type: '(event:ReactUIEvent)=>void',
                    defValue: 'undefined',
                },
                {
                    name: 'style',
                    desc: '可选，容器的行内样式',
                    type: ' CSSProperties',
                    defValue: '{}',
                },
            ]}
        />
    </div>
}

export default FlexBoxView;
